<template>
    <div :class="['pop-full', { 'is-visible': isVisible }]" v-if="isVisible" id="pop-prdfull">
        <div class="dim"></div> <!--    dim  -->
        <div class="pop-wrap">
            <div class="pop-title2">
                <h3 class="title-type">{{ props.lookBookData.title }}</h3> <!-- LookBook   -->
                <button @click="closePopup" class="pop-close"><i class="ico-close"></i><span
                        class="hidden"></span></button> <!--    -->
            </div>
            <div class="pop-content">
                <div class="product-top">
                    <div class="prd-mode">
                        <span class="count"><em>{{ props.lookBookData.products.length }}</em> items</span> <!--    -->
                    </div>
                </div>
                <div class="product-list">
                    <ul>
                        <li class="product-item" v-for="(product, index) in props.lookBookData.products" :key="index">
                            <button class="btn-wish">
                                <span class="hidden">wish</span> <!--   -->
                            </button>
                            <div class="product-cont">
                                <a href="#">
                                    <div class="product-model">
                                        <div class="img-box">
                                            <img :src="product.image" :alt="product.alt"> <!--    alt  -->
                                        </div>
                                    </div>
                                    <div class="product-info">
                                        <div class="product-title">
                                            <h3 class="brand">{{ product.brand }}</h3> <!--   -->
                                            <p class="title">{{ product.title }}</p> <!--   -->
                                        </div>
                                        <div class="product-price">
                                            <span class="original" v-if="product.originalPrice"><b>{{ product.originalPrice }}</b></span> <!--  ,   -->
                                            <div class="special-price">
                                                <strong class="price"><b>{{ product.price }}</b></strong> <!--    -->
                                                <em class="sale" v-if="product.sale">{{ product.sale }}%</em> <!--  ,   -->
                                            </div>
                                        </div>
                                        <div class="product-bottom">
                                            <div class="tag-box">
                                                <span class="tag-type" v-for="tag in product.tags" :key="tag">{{ tag }}</span> <!--    -->
                                            </div>
                                            <div class="stats-box">
                                                <span class="like">
                                                    <i class="ico-star"><span class="hidden">like</span></i>
                                                    <em>{{ product.likes }}</em> <!--    -->
                                                </span>
                                                <span class="color">
                                                    <i class="ico-plus v2"><span class="hidden">more</span></i>
                                                    <em>{{ product.colors }}</em> <!--     -->
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </a>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref, watch, defineProps, defineExpose } from 'vue';

// props , LookBook  
const props = defineProps<{
    lookBookData: {
        title: string;
        products: Array<{
            image: string;
            alt: string;
            brand: string;
            title: string;
            originalPrice?: string; //  (optional)
            price: string;
            sale?: number; //  (optional)
            tags: string[];
            likes: number;
            colors: string;
        }>
    }
}>();

//    
const isVisible = ref(false);

//   
const openPopup = () => {
    isVisible.value = true;
};

//   
const closePopup = () => {
    isVisible.value = false;
};

//    ,    
watch(isVisible, (newVal) => {
    document.body.style.overflow = newVal ? 'hidden' : '';
});

//   openPopup, closePopup    
defineExpose({ openPopup, closePopup });
</script>
