<template>
    <!--     -->
    <div class="info-box">
        <div class="accordion-list">
            <!--      -->
            <dl v-for="(item, index) in localItems" :key="index" class="accordion-item" :class="{ active: item.isActive }">
                <dt>
                    <!--  ,    -->
                    <button class="js_accordion" @click="toggleAccordion(index)">
                        {{ item.title }}
                    </button>
                </dt>
                <dd :class="{ isOpen: item.isActive }" style="max-height: 0;">
                    <!--  , HTML  -->
                    <div v-html="item.content" class="items"></div>
                   
                </dd>
            </dl>
        </div>

    </div>
</template>

<script setup lang="ts">
import { onMounted, nextTick } from 'vue'; // Vue   import
import { useAccordion } from '../../../utils/useAccordion'; //   

// AccordionItem  
interface AccordionItem {
    title: string;  //  
    content: string; //   (HTML  )
    isActive: boolean; //  
}

// Props    
const props = defineProps<{
    items: AccordionItem[]; //   
}>();

// useAccordion      
const { localItems, toggleAccordion } = useAccordion(props.items);

//   ,     
onMounted(() => {
    // DOM    
    nextTick(() => {
        //       
        localItems.value.forEach((item, index) => {
            const element = document.querySelectorAll('.accordion-item')[index]?.querySelector('dd') as HTMLElement; //   dd  
            const itemsElement = element.querySelector('.items') as HTMLElement; // dd  items  
            if (element && itemsElement) {
                const totalHeight = itemsElement.scrollHeight; //    

                if (item.isActive) {
                    element.style.maxHeight = `${totalHeight}px`; //     
                } else {
                    element.style.maxHeight = '0'; //    0 
                }
            }
        });
    });
});
</script>
