import { nextTick } from 'vue';
import type { Swiper as SwiperType } from 'swiper';
import Vimeo from '@vimeo/player';

// useVimeo  
export function useVimeo() {
  // Vimeo   
  const initVimeo = (swiper: SwiperType | null) => {
    nextTick(() => { // Vue    
      let vimeoArea: NodeListOf<Element> | null = null;  //    
      let activeSwiper: Element | null = null;  //      

      if (swiper) {  // Swiper   
        vimeoArea = (swiper.el as HTMLElement).querySelectorAll('.video-area');  //    
        activeSwiper = (swiper.el as HTMLElement).querySelector('.swiper-slide-active');  //    
      } else {  // Swiper   
        vimeoArea = document.querySelectorAll('.video-area.normal');  //    
      }

      if (!vimeoArea || vimeoArea.length === 0) return;  //    

      const vimeoPlayers: Vimeo[] = [];  // Vimeo   

      vimeoArea.forEach((element, index) => {  //     
        const iframe = element.querySelector('iframe') as HTMLIFrameElement;  // iframe  
        if (!iframe) return;  // iframe   

        const controls = iframe.src.includes('controls');  // iframe src 'controls'   
        element.setAttribute('data-index', index.toString());  //  data-index  

        const player = new Vimeo(iframe);  // Vimeo   
        vimeoPlayers[index] = player;  // Vimeo   
        player.setAutopause(false);  //     

        player.ready().then(() => {  //    
          if (controls) {  // controls  
            player.on('ended', () => {  //    
              const thumb = element.querySelector('.thumb') as HTMLElement;  //   
              if (thumb) {  //   
                thumb.style.pointerEvents = 'auto';  //    
                thumb.style.opacity = '1';  //   
                const img = thumb.querySelector('img');  //   
                if (img) img.style.display = 'block';  //  
              }
            });
          }
        });
      });

      if (swiper) {  // Swiper   
        swiper.on('slideChangeTransitionStart', () => {  //     
          vimeoArea?.forEach((element, index) => {  //    
            if (!element.classList.contains('loop')) {  //   
              vimeoPlayers[index].getCurrentTime().then((seconds) => {  //    
                if (Number(seconds) > 0) {  //   0 
                  vimeoPlayers[index].pause();  //   
                }
              });
            }
          });
        });

        swiper.on('slideChangeTransitionEnd', () => {  //     
          handleVideoPlayback();  //     
        });

        //    
        const handleVideoPlayback = () => {
          activeSwiper = (swiper.el as HTMLElement).querySelector('.swiper-slide-active');  //    
          if (!activeSwiper) return;

          const activeVimeoIndex = activeSwiper.querySelector('.video-area')?.getAttribute('data-index');  //    
          if (!activeVimeoIndex) return;

          const videoArea = activeSwiper.querySelector('.video-area');  //   
          const videoPlayer = vimeoPlayers[Number(activeVimeoIndex)];  // Vimeo  

          if (videoArea && !videoArea.classList.contains('loop') && !activeSwiper.querySelector('.btn-play')) {
            if (swiper.autoplay) {
              swiper.autoplay.stop();  //    Swiper   
            }

            videoPlayer.play().then(() => {  //    
              if (swiper.autoplay) {
                swiper.autoplay.start();  //    Swiper   
              }
            }).catch(() => {  //    
              if (swiper.autoplay) {
                swiper.autoplay.start();  //     Swiper   
              }
            });
          }
        };

        handleVideoPlayback();  //    
      }
    });
  };

  return {
    initVimeo,  // initVimeo       
  };
}
