import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router';
import GuidePage from '@/pages/Guide.vue'; //   .
import UiGuide from '@/pages/mo/guide/GuideIndex.vue';
import ColorGuide from '@/pages/mo/guide/Color/ColorGuide.vue';
import FontGuide from '@/pages/mo/guide/Fonts/FontGuide.vue';
import TextGuide from '@/pages/mo/guide/Text/TextGuide.vue';
import ButtonGuide from '@/pages/mo/guide/Buttons/ButtonGuide.vue';
import FormGuide from '@/pages/mo/guide/Form/FormGuide.vue';
import ComponentGuide from '@/pages/mo/guide/Component/ComponentGuide.vue';
import IconsGuide from '@/pages/mo/guide/Icons/Index.vue';
import TableGuide from '@/pages/mo/guide/Table/TableGuide.vue';
import SwiperGuide from '@/pages/mo/guide/Swiper/SwiperGuide.vue';
import PopupGuide from '@/pages/mo/guide/Popup/PopupGuide.vue';
import VimeoGuide from '@/pages/mo/guide/Vimeo/VimeoGuide.vue';
import MainPage from '@/pages/MainPage.vue'; //   
import WishListPage from '@/pages/WishList/WishListPage.vue'; //   
import MyInfoPage from '@/pages/MyInfo/MyInfoPage.vue'; //    
import BrandPage from '@/pages/Brand/BrandPage.vue'; //   
import CancelPage from '@/pages/Mypage/RequestCancel.vue'; //   
import ReturnPage from '@/pages/Mypage/RequestReturn.vue'; //   
import MyaddRessPage from '@/pages/Mypage/MyAddress.vue'; //    
import ProductViewPage from '@/pages/Product/ProductView.vue'; // 
import ProductlistPage from '@/pages/Product/list_seller.vue'; // 
import ErrorPage from '@/pages/Error/ErrorPage.vue'; // 404
import DisplayTypeA from '@/pages/Display/detail_display_A.vue'; 
import DisplayTypeAscroll from '@/pages/Display/detail_display_A_scroll.vue'; 
import DisplayTypeB from '@/pages/Display/detail_display_B.vue'; 
import DisplayTypeBscroll from '@/pages/Display/detail_display_B_scroll.vue'; 
import DetailBrandPage from '@/pages/Brand/DetailBrands.vue'; 

//     
const routes: Array<RouteRecordRaw> = [
  {
    path: '/', // URL 
    name: 'Guide', //  
    component: GuidePage //   
  },
  { 
    path: '/ui-guide',
    name: 'UiGuide',
    component: UiGuide
  },
  {
    path: '/color-guide',
    name: 'ColorGuide',
    component: ColorGuide
  },
  {
    path: '/font-guide',
    name: 'FontGuide',
    component: FontGuide
  },
  {
    path: '/text-guide',
    name: 'TextGuide',
    component: TextGuide
  },
  {
    path: '/button-guide',
    name: 'ButtonGuide',
    component: ButtonGuide
  },
  {
    path: '/form-guide',
    name: 'FormGuide',
    component: FormGuide
  },
  {
    path: '/component-guide',
    name: 'ComponentGuide',
    component: ComponentGuide
  },

  {
    path: '/icons-guide',
    name: 'IconsGuide',
    component: IconsGuide
  },
  {
    path: '/table-guide',
    name: 'TableGuide',
    component: TableGuide
  },
  {
    path: '/swiper-guide',
    name: 'SwiperGuide',
    component: SwiperGuide
  },
  {
    path: '/popup-guide',
    name: 'PopupGuide',
    component: PopupGuide
  },
  {
    path: '/vimeo-guide',
    name: 'VimeoGuide',
    component: VimeoGuide
  },

  {
    path: '/main',
    name: 'Home',
    component: MainPage, //   .
  },

  {
    path: '/favorites',
    name: 'WishList',
    component: WishListPage, //   .
  },

  {
    path: '/me',
    name: 'MyInfo',
    component: MyInfoPage, //    .
  },

  {
    path: '/brands',
    name: 'Brand',
    component: BrandPage, //   .
  },
  {
    path: '/cancle',
    name: 'Cancle',
    component: CancelPage, //   .
  },
  {
    path: '/return',
    name: 'Return',
    component: ReturnPage, //   .
  },
  {
    path: '/myaddress',
    name: 'myaddress',
    component: MyaddRessPage, //     .
  },
  {
    path: '/productView',
    name: 'productView',
    component: ProductViewPage, //    
  },
  {
    path: '/productList',
    name: 'productList',
    component: ProductlistPage, //  2   
  },
  {
    path: '/:catchAll(.*)', //         .
    name: 'Error',
    component: ErrorPage,
  },
  {
    path: '/displaya',
    name: 'DisplayA',
    component: DisplayTypeA
  },
  {
    path: '/displayascroll',
    name: 'DisplayAscroll',
    component: DisplayTypeAscroll
  },
  {
    path: '/displayb',
    name: 'displayb',
    component: DisplayTypeB
  },
  {
    path: '/displaybscroll',
    name: 'DisplayBscroll',
    component: DisplayTypeBscroll
  },
  {
    path: '/DetailBrand',
    name: 'DetailBrand',
    component: DetailBrandPage
  },

  
  
];

// Vue Router  .
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL), // HTML5 History    URL  
  routes, //     .
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { left: 0, top: 0 };
    }
  },
});

export default router; //      .