//          
const handleMenuClick = (index: number) => {
    const selectedMenu = menus[index];

    switch (selectedMenu) {
        case 'TOP BRANDS':
            showTopBrandList.value = true;    // TopBrandList 
            showExclusiveBrandList.value = false;  // ExclusiveBrandList 
            showMyBrandList.value = false;  // MyBrandList 
            showWordSwiper.value = false;    // WordSwiper 
            break;

        case 'EXCLUSIVE BRANDS':
            showExclusiveBrandList.value = true;   // ExclusiveBrandList 
            showTopBrandList.value = false;  // TopBrandList 
            showMyBrandList.value = false;   // MyBrandList 
            showWordSwiper.value = false;    // WordSwiper 
            break;

        case 'MY':
            showMyBrandList.value = true;    // MyBrandList 
            showTopBrandList.value = false;  // TopBrandList 
            showExclusiveBrandList.value = false;  // ExclusiveBrandList 
            showWordSwiper.value = false;    // WordSwiper 
            break;

        default:
            showWordSwiper.value = true;     // WordSwiper 
            showTopBrandList.value = false;  // TopBrandList 
            showExclusiveBrandList.value = false;  // ExclusiveBrandList 
            showMyBrandList.value = false;   // MyBrandList 
            break;
    }
};

