<template>
    <div class="top-title">
      <p class="tit">{{ props.title }}</p>
      <button class="btn-wish" @click="handleWishClick" ref="wishButton">
        <span class="hidden">wish</span>
      </button>
    </div>
  </template>
  
  <script setup lang="ts">
  import { defineProps, defineEmits, ref } from 'vue';
  
  // Props 
  const props = defineProps<{
    title: string; //  
  }>();
  
  // Emit 
  const emit = defineEmits<{
    (e: 'wishClick'): void;
  }>();
  
  //     
  const wishButton = ref<HTMLButtonElement | null>(null);
  
  // wish   
  const handleWishClick = () => {
    if (wishButton.value) {
      wishButton.value.classList.toggle('active'); // active  
    }
    emit('wishClick');
  };
  </script>