<template>
  <div class="product-list">
    <ul :class="[customClass]">
      <!-- products   ProductItem   -->
      <!-- showRanking true , ranking index + 1    -->
      <ProductItem v-for="(product, index) in props.products" :showRanking="showRanking" :ranking="index + 1" :key="index" :product="product" />
      <!--     -->
      <li v-if="showPromotion" class="product-item product-promotion">
        <router-link to="/">
          <img src="@/assets/mo/images/temp/@temp_prd_banner.png" alt="Promotion Banner" />
        </router-link>
      </li>
      <!-- //    -->
    </ul>
  </div>
</template>

<script setup lang="ts">
import { defineProps } from 'vue';  // Vue defineProps  
import ProductItem from './ProductItem.vue';  // ProductItem  
import { Product } from './ProductTypes';  // Product  

//    props 
const props = defineProps<{
  products: Product[];  // Product    
  customClass?: string;  //  CSS 
  showRanking?: boolean; 
  showPromotion?: boolean;  //      
}>();

</script>

