const handleOptionSelected = (option: string | null) => {
    //    depthProducts    
    if (!option || !depthProducts.value) return;

    //     .  btn-option 2 2(  ) 
    // const btnOptions = btnWrapRef.value?.querySelectorAll('.btn-option') || [];
    // const hasDepth02 = btnOptions.length === 2;

    // nextTick  DOM   
    nextTick(() => {
        let targetElement: HTMLElement | null = null; //      
        let targetIndex: number | null = null; //     
        let hasDepth01 = false; // depth-01    

        if (depthProducts.value) {
            //          
            depthProducts.value.forEach((product, index) => {
                const depth01Element = product.querySelector('.depth-01'); // 1  
                const depth02Elements = product.querySelectorAll('.depth-02'); // 2  

                //  depth-01   
                if (depth01Element && depth01Element.textContent?.trim() === option) {
                    targetElement = depth01Element as HTMLElement; //    
                    targetIndex = index; //   
                    hasDepth01 = true; // depth-01 
                }

                //  depth-02   
                depth02Elements.forEach((depth02Element) => {
                    if (depth02Element.textContent?.trim() === option) {
                        targetElement = depth02Element as HTMLElement; //    
                        targetIndex = index; //   
                        if (!depth01Element) hasDepth01 = false; // depth-01   
                    }
                });

                // depth02    depth01   
                if (depth02Elements.length === 0 && depth01Element && depth01Element.textContent?.trim() === option) {
                    targetElement = depth01Element as HTMLElement; //   
                    targetIndex = index; //   
                    hasDepth01 = true; // depth-01 
                }
            });
        }

        //     
        if (targetElement && targetIndex !== null) {
            const rect = targetElement.getBoundingClientRect(); //     
            const elementTop = rect.top + scrollTop; //     
            let scrollMove;

            //   
            if (scrollTop < elementTop) {
                //     
                if (hasDepth01) {
                    scrollMove = elementTop; // depth-01    
                } else {
                    scrollMove = elementTop - 38; // depth-01    
                }
            } else {
                //     
                if (hasDepth01) {
                    scrollMove = elementTop - 50; // depth-01    
                } else {
                    scrollMove = elementTop - 100; // depth-01    
                }
            }

            gsap.to(window, { duration: 1, scrollTo: { y: scrollMove, autoKill: false } });

            //       
            lastScrollTop = scrollTop;
        }
    });
};



