<template>
    <div class="scr-item plan-count">
      <p class="title">{{ timerProps.title }}</p>
      <div class="count-list">
        <div class="count-item">
          <div class="number">{{ days }}</div>
          <div class="label">DAYS</div>
        </div>
        <div class="count-colon">:</div>
        <div class="count-item">
          <div class="number">{{ hours }}</div>
          <div class="label">HRS</div>
        </div>
        <div class="count-colon">:</div>
        <div class="count-item">
          <div class="number">{{ minutes }}</div>
          <div class="label">MINS</div>
        </div>
        <div class="count-colon">:</div>
        <div class="count-item">
          <div class="number">{{ seconds }}</div>
          <div class="label">SECS</div>
        </div>
      </div>
    </div>
  </template>
  
  <script setup lang="ts">
  import { ref, onMounted, onUnmounted } from 'vue';
  
  // Props 
  const props = defineProps<{
    timerProps: {
      title: string;  //  
      timer: string;  //   (: YYYYMMDD)
    };
  }>();
  
  //   
  const days = ref(0);
  const hours = ref(0);
  const minutes = ref(0);
  const seconds = ref(0);
  
  let countdownInterval: ReturnType<typeof setInterval> | null = null;
  let alertTriggered = false; //   
  
  //   Date   
  const parseDate = (dateString: string): Date => {
    const year = parseInt(dateString.slice(0, 4));
    const month = parseInt(dateString.slice(4, 6)) - 1; //  0 
    const day = parseInt(dateString.slice(6, 8));
    return new Date(year, month, day, 0, 0, 0);
  };
  
  //   
  const updateTimer = () => {
    const targetDate = parseDate(props.timerProps.timer);
    const now = new Date();
    const timeDifference = targetDate.getTime() - now.getTime();
  
    if (timeDifference <= 0) {
      if (countdownInterval !== null) {
        clearInterval(countdownInterval);
      }
      days.value = 0;
      hours.value = 0;
      minutes.value = 0;
      seconds.value = 0;
  
      if (!alertTriggered) { // alert    
        alert('Time is up!'); //     
        alertTriggered = true; //  
      }
      return;
    }
  
    days.value = Math.floor(timeDifference / (1000 * 60 * 60 * 24));
    hours.value = Math.floor((timeDifference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    minutes.value = Math.floor((timeDifference % (1000 * 60 * 60)) / (1000 * 60));
    seconds.value = Math.floor((timeDifference % (1000 * 60)) / 1000);
  };
  
  //     
  onMounted(() => {
    updateTimer(); //   
    countdownInterval = setInterval(updateTimer, 1000); // 1  
  });
  
  //     
  onUnmounted(() => {
    if (countdownInterval !== null) {
      clearInterval(countdownInterval);
    }
  });
  </script>
  