<template>
    <div v-if="isVisible" class="top-banner top-img" id="top-banner" ref="topBannerElement">
        <button type="button" class="btn-close" @click="closeBanner"><i class="ico-close"></i></button>
        <swiper :observer="true" :observe-parents="true" :slides-per-view="1" class="topbanner-swiper"
            :autoplay="autoplayOptions" :speed="500" :modules="modules" effect="fade">
            <swiper-slide v-for="(item, index) in props.tabSlides" :key="index" :style="{ backgroundColor: item.bgColor }">
                <div class="brand-box">
                    <img :src="item.imgSrc" :alt="item.alt" />
                    <div class="text-box" v-html="item.text"></div>
                </div>
            </swiper-slide>
        </swiper>
    </div>
</template> 

<script setup lang="ts">
import { ref, onMounted, defineEmits } from 'vue';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import { Autoplay, EffectFade } from 'swiper';

// Swiper     
const autoplayOptions = {
    delay: 3000,  // 3  
    disableOnInteraction: false,  //     
};

const modules = [Autoplay, EffectFade];  // Swiper   

// props 
const props = defineProps<{
    tabSlides: Array<{ imgSrc: string; alt: string; text: string; link: string, bgColor: string }>;  //   
}>();

// emit  
const emit = defineEmits<{
    (e: 'updateHeight', height: number): void;  //      
    (e: 'updateVisibility', isVisible: boolean): void;  //       
}>();

//    
const topBannerElement = ref<HTMLElement | null>(null);

//    
const isVisible = ref(true);

//   
const closeBanner = () => {
    isVisible.value = false;  //   false   
    emit('updateVisibility', isVisible.value);  //     
    //        null 
    emit('updateHeight', 0);  //   0 
};

//         
onMounted(() => {
    if (topBannerElement.value) {
        const bannerHeight = topBannerElement.value.clientHeight;  //   
        emit('updateHeight', bannerHeight);  //     
    }
});

</script>
